h4 {
  margin-bottom: 0;
}

p {
  margin-top: 0;
}

ol {
  padding: 0;
}

.accordion {
  background-color: #eee;
  color: #444;
  cursor: pointer;
  // padding: 18px;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
  transition: 0.4s;
}

.active,
.accordion:hover {
  background-color: #ccc;
}

.accordion:focus {
  border: none;
  outline: none;
}

.panel {
  padding: 0 18px;
  background-color: white;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
}

.load-more {
  padding-bottom: 30px;
  padding-top: 30px;
  text-align: center;
  width: 100%;
}
.load-more a {
  background: #faa635;
  border-radius: 3px;
  color: white;
  display: inline-block;
  padding: 10px 30px;
  transition: all 0.25s ease-out;
  -webkit-font-smoothing: antialiased;
  text-decoration: none;
}
.load-more a:hover {
  background-color: #042a63;
}

.container {
  max-width: 1200px;
  padding: 15px;
}
